<template>
  <v-app id="login">
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <vue-particles
            id="particles-js"    	
        color="#7cb79d"
        :particleOpacity="0.7"
        :particlesNumber="80"
        shapeType="circle"
        :particleSize="4"
        linesColor="#7cb79d"
        :linesWidth="1"
        :lineLinked="true"
        :lineOpacity="0.4"
        :linesDistance="150"
        :moveSpeed="3"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="true"
        clickMode="push" 
              >
          </vue-particles>
          <v-flex xs12 sm8 md4 lg4>
            <v-card class="elevation-1 pa-3">
              <v-card-text>
                <div class="layout column align-center">
                  <img src="static/logo.png" alt="Vue Material Admin" width="180" height="180">
                  <h1 style= "color: #008F7A">WBSpider</h1>
                </div>
                <v-form :model="loginRuleForm" >
                  <v-text-field
                    append-icon="person"
                    name="login"
                    label="/"
                    type="text"
                    v-model="loginRuleForm.username"
                    :error="error"
                    :rules="[rules.required]"/>
                  <v-text-field
                    :type="hidePassword ? 'password' : 'text'"
                    :append-icon="hidePassword ? 'visibility_off' : 'visibility'"
                    name="password"
                    label=""
                    id="password"
                    :rules="[rules.required]"
                    v-model="loginRuleForm.password"
                    :error="error"
                    @click:append="hidePassword = !hidePassword"/>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-btn 
                  class="white--text"
                  color="#008F7A"
                  @click="Go"
                >
                </v-btn>
                <v-spacer></v-spacer>
                <v-btn 
                  :disabled="dialog"
                  :loading="dialog"
                  class="white--text"
                  color="#008F7A"
                  @click="login"
                >
                </v-btn>
              <v-dialog
                v-model="dialog"
                hide-overlay
                persistent
                width="300"
              >
                <v-card
                  color="primary"
                  dark
                >
                  <v-card-text>
                    ,
                    <v-progress-linear
                      indeterminate
                      color="white"
                      class="mb-0"
                    ></v-progress-linear>
                  </v-card-text>
                </v-card>
              </v-dialog>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
      <v-snackbar
        v-model="showResult"
        :timeout="2000"
        top>
        {{ result }}
      </v-snackbar>
    </v-content>
  </v-app>

</template>

<script>
export default {
  data() {
    return {
      // 
      loginRuleForm: {
        username: '',
        password: '',
      },
      dialog: false,
      hidePassword: true,
      error: false,
      showResult: false,
      result: '',
      rules: {
        required: value => !!value || 'Required.'
      }
    }
  },

  methods: {
    login() {
      const vm = this;

      if (!vm.loginRuleForm.username || !vm.loginRuleForm.password) {

        vm.result = "Email and Password can't be null.";
        vm.showResult = true;

        return;
      }
      vm.dialog = true;
      // formdata this.qs.stringify()
      const params = this.qs.stringify(this.loginRuleForm)
      this.$axios.post("/api/checklogin", params).then((res) => {
        if (res.data == 0){
          vm.dialog = false;
          this.$message.success("Hi " + this.loginRuleForm.username + " !")
          return vm.$router.push({ name: 'Start' }); // 
        }
        else{
          vm.dialog = false;
          vm.error = true;
          vm.result = "Email or Password is incorrect.";
          vm.showResult = true;
        }

      })
    },
    Go(){
      const vm = this;
      return vm.$router.push({ name: 'Start' });
    }
  }
}
</script>

<style scoped lang="css">
  #login {
    height: 50%;
    width: 100%;
    position: relative;
    top: 0;
    left: 0;
    content: "";
    z-index: 0;
  }
  #particles-js{
    width: 100%;
    height: 90%;
    position: absolute;
}
</style>
         


