<template>
  <div class="box">
    <div class="login_logo">
      <img src="../assets/logo.png" alt="" />
    </div>
    <el-input
      placeholder=""
      v-model="searchNameForm.uname"
      class="input-with-select"
    >
      <el-select v-model="select" slot="prepend" placeholder="">
        <el-option label="" value="1"></el-option>
        <el-option label="uid" value="2"></el-option>
      </el-select>
    </el-input>
      <v-btn round class="button1" color="#008F7A" :disabled="dialog1" :loading="dialog1" @click="start"><span class="white--text"></span></v-btn>
      <v-btn round class="button2" color="#008F7A" :disabled="dialog2" :loading="dialog2" @click="proxy"><span class="white--text"></span></v-btn>
        <v-dialog
          v-model="dialog2"
          hide-overlay
          persistent
          width="300"
        >
          <v-card
            color="#008F7A"
            dark
          >
            <v-card-text>
              ,....
              <v-progress-linear
                indeterminate
                color="white"
                class="mb-0"
              ></v-progress-linear>
            </v-card-text>
          </v-card>
        </v-dialog>
        <v-dialog
          v-model="dialog1"
          hide-overlay
          persistent
          width="300"
        >
          <v-card
            color="#008F7A"
            dark
          >
            <v-card-text>
              {{ messages }}
              <v-progress-linear
                indeterminate
                color="white"
                class="mb-0"
              ></v-progress-linear>
            </v-card-text>
          </v-card>
        </v-dialog>
      <v-snackbar
        v-model="showResult"
        :timeout="2000"
        top>
        {{ result }}
      </v-snackbar>
    <div>
      <v-spacer></v-spacer>
    </div>
    <el-divider ></el-divider>
  </div>
   
</template>

<script>
import Vue from "vue";
export default {
  data() {
    return {
      select : "",
      messages: "",
      searchNameForm: {
        uname: "",
      },
      dialog1: false,
      dialog2: false,
      error: false,
      showResult: false,
      result: '',
    };
  },
  mounted() {
    this.note();
  },
  methods: {
    note() {
      const h = this.$createElement;
      this.$notify.info({
        title: "",
        message: h(
          "i",
          { style: "color: teal" },
          ",,coookie!"
        ),
        position: "bottom-right",
        duration: 5000,
      });
    },
    async getInfo() {
      this.messages = "...";
      const params = this.qs.stringify(this.searchNameForm);
      await this.$axios.post("/api/search", params).then((res) => {
        Vue.prototype.$res_data_user = res.data;
      });
    },
    async getStatuses() {
      this.messages = "...";
      await this.$axios.get("/api/statuses").then((res) => {
        Vue.prototype.$res_data_statuses = res.data.data.list;
      });
    },
    async getRelation() {
      this.messages = "...";
      await this.$axios.get("/api/friends").then((res) => {
        Vue.prototype.$res_data_friends = res.data.users;
      });
      await this.$axios.get("/api/fans").then((res) => {
        Vue.prototype.$res_data_fans = res.data.users;
      });
    },
    async getComments(){
      this.messages = "...";
      await this.$axios.get("/api/comments").then((res) => {
          localStorage.setItem('COMMENTS', JSON.stringify(res.data))
          this.result = "!";
          this.dialog1 = false;
          this.error = false;
          this.showResult = true;
      });
    },

    supervise(){
      console.log("check for supervise")
      this.$axios.get("/api/check").then((res) => {
        if (res.data != -1){
          Vue.prototype.$res_data_statuses = res.data.data.list;
          const h = this.$createElement;
          this.$notify.info({
            title: "",
            message: h(
              "i",
              { style: "color: teal" },
              ", !"
            ),
            position: "bottom-left",
            duration: 5000,
          });
        }
        else{
          const h = this.$createElement;
          this.$notify.info({
            title: "",
            message: h(
              "i",
              { style: "color: teal" },
              ", ..."
            ),
            position: "bottom-left",
            duration: 5000,
          });
        }
      });      
    },
    async start(){
      this.dialog1 = true;
      await this.getInfo();
      await this.getStatuses();
      await this.getRelation();
      await this.getComments();
      setInterval(this.supervise,30000);
    },
    proxy(){
      const vm = this;
      vm.dialog2 = true;
      this.$axios.get("/api/proxy").then((res) => {
        if (res.data == 1){
          vm.dialog2= false;
          vm.error = false;
          vm.result = ".";
          vm.showResult = true;
        }
        else{
          vm.dialog2 = false;
          vm.error = true;
          vm.result = ".";
          vm.showResult = true;
        }

      })
    }
  },
};
</script>


<style>
.el-select .el-input {
  width: 130px;
}
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}
.box {
  
  width: 50%;
  position: relative;
  left:300px;
  /*  */
}
.button1 {
  position: relative;
  left:400px;
  bottom:-17px;
}

.button2 {
  position: relative;
  left:100px;
  bottom:-17px;
}

.login_logo img {
  width: 40%;
  height: 40%;
  position: relative;
  left:200px;
  background: rgb(255, 255, 255);
}
</style>


