<template>
  <div class="search" >
    <div >
      <div v-show="isShow">
      <div class="avator">
      <img :src="this.$res_data_user.avatar_url"  alt="" />
      </div>
      <el-divider ></el-divider>
    <el-descriptions
      class="margin-top"
      title=""
      :column="3"
      :size="size"
      border
      v-loading="loading"
      style="width: 100%"
    >
      <el-descriptions-item class-name="usrname">
        <template slot="label">
          <i class="el-icon-user"></i>
          
        </template>
        {{this.$res_data_user.usname}}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-female"></i>
          
        </template>
        {{this.$res_data_user.gender}}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-cherry"></i>
          
        </template>
        {{this.$res_data_user.birth}}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-map-location"></i>
          
        </template>
        {{this.$res_data_user.address}}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-map-location"></i>
          ip
        </template>
        {{this.$res_data_user.ip}}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-alarm-clock"></i>
          
        </template>
        {{ this.$res_data_user.time }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-wind-power"></i>
          
        </template>
        {{this.$res_data_user.counts}}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-potato-strips"></i>
          
        </template>
        {{ this.$res_data_user.fans }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-pear"></i>
          
        </template>
        {{ this.$res_data_user.friends }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-position"></i>
          
        </template>
        {{ this.$res_data_user.id }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-mic"></i>
          
        </template>
        {{ this.$res_data_user.des }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-medal"></i>
          
        </template>
        {{this.$res_data_user.pro}}
      </el-descriptions-item>
    </el-descriptions>
      </div>
      <el-divider ></el-divider>
    </div>
    <div>
      <v-spacer></v-spacer>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: true,
    };
  },
  methods: {
  },
};
</script>

<style scoped>

  .avator {
    /*  */
    width: 150px;
    /*  */
    height: 150px;
    /*  */
    position:relative;
    /*  */
    top: 30px;
    /*  */
    left: 49%;
    /* logo */
    margin-left: -75px;
    /*  */
    border: 1px solid rgb(255, 255, 255);
    /*  */
    border-radius: 50%;
    /*  */
    background: rgb(255, 255, 255);
    /*  */
    padding: 10px;
    /*  */
    box-shadow: 0 0 2px 2px rgb(255, 255, 255);
  }
  .avator img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background: rgb(238, 238, 238);
  }
</style>


