import Vue from 'vue';
import Router from 'vue-router';

import Start from '../pages/Start.vue';
import Statuses from '../pages/Statuses.vue';
import Comments from '../pages/Comments.vue';
import Relation from '../pages/Relation.vue';
import UserInfo from '../pages/UserInfo.vue';
import Media from '../pages/Media.vue';
import Snackbar from '../pages/Snackbar.vue';
import Login from '../pages/core/Login.vue';
import Error from '../pages/core/Error.vue';



Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Start',
      component: Start,
      meta: {
        breadcrumb: [
          { name: '' }
        ]
      }
    },
    {
      path: '/relation',
      name: 'Relation',
      component: Relation,
      meta: {
        breadcrumb: [
          { name: '', href: 'Start' },
          { name: '' }
        ]
      }
    },
    {
      path: '/snackbar',
      name: 'Snackbar',
      component: Snackbar,
      meta: {
        breadcrumb: [
          { name: '', href: 'Start' },
          { name: 'snackbar' }
        ]
      }
    },
    {
      path: '/user',
      name: 'UserInfo',
      component: UserInfo,
      meta: {
        breadcrumb: [
          { name: '', href: 'Start' },
          { name: '' }
        ]
      }
    },
    {
      path: '/statuses',
      name: 'Statuses',
      component: Statuses,
      meta: {
        breadcrumb: [
          { name: '', href: 'Start' },
          { name: '' }
        ]
      }
    },
    {
      path: '/media',
      name: 'Media',
      component: Media,
      meta: {
        breadcrumb: [
          { name: 'Start', href: 'Start' },
          { name: 'media' }
        ]
      }
    },
    {
      path: '/comments',
      name: 'Comments',
      component: Comments,
      meta: {
        breadcrumb: [
          { name: '', href: 'Start' },
          { name: '' }
        ]
      }
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
      meta: {
        allowAnonymous: true
      }
    },
    {
      path: '/error',
      name: 'Error',
      component: Error,
      meta: {
        allowAnonymous: true
      }
    },
  ]
});



