<template>
  <div>
    <template v-if="!$route.meta.allowAnonymous">
      <v-app id="inspire">
        <div class="app-container">
          <toolbar @toggleNavigationBar="drawer = !drawer"/>
          <navigation :toggle="drawer"/>
          <v-content>
            <breadcrumbs />
            <router-view/>
            <page-footer />
          </v-content>
        </div>
      </v-app>
    </template>
    <template v-else>
      <transition>
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </transition>
    </template>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      drawer: true
    }
  },
  created() {
      setTimeout(() => {
          window.L2Dwidget.init({
              pluginRootPath: '../static/live2dw/',
              pluginJsPath: 'lib/',
              pluginModelPath: 'live2d-widget-model-haru_1/assets/', //haru_2,,
              tagMode: false,
              debug: false,
              model: { jsonPath: '../static/live2dw/live2d-widget-model-haru_1/assets/haru01.model.json' }, 
              display: { position: 'left', width: 225, height: 500,hOffset:40 },  //,
              mobile: { show: true },   //
              log: false,
          })
      }, 1000)
  }

}
</script>

<style>
  .v-btn:hover:before{
    color: transparent !important;
  }
</style>



